import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ScanPage } from '../scan/scan';
import { VoucherProvider } from '../../providers/voucher/voucher';
import { Product } from '../../model/product';

/**
 * Generated class for the ProductsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
	selector: 'page-products',
	templateUrl: 'products.html',
})
export class ProductsPage {

	public products: Array<Product> = [
		{
			'id': 1,
			'name': 'soap',
			'image': '../../assets/imgs/soap.jpg',
			'unit': 'Unit'
		},
		{
			'id': 2,
			'name': 'toothbrush',
			'image': '../../assets/imgs/toothbrush.jpg',
			'unit': 'KG'
		},
		{
			'id': 3,
			'name': 'pear',
			'image': '../../assets/imgs/pear.jpg',
			'unit': 'KG'
		},
		{
			'id': 4,
			'name': 'rice',
			'image': '../../assets/imgs/rice.jpg',
			'unit': 'KG'
		},
		{
			'id': 5,
			'name': 'flour',
			'image': '../../assets/imgs/flour.jpg',
			'unit': 'KG'
		},
		{
			'id': 6,
			'name': 'toothpaste',
			'image': '../../assets/imgs/toothpaste.jpg',
			'unit': 'Unit'
		},
		{
			'id': 7,
			'name': 'apple',
			'image': '../../assets/imgs/apples.jpeg',
			'unit': 'KG'
		},
		{
			'id': 8,
			'name': 'brush',
			'image': 'data:image/jpeg;base64,',
			'unit': ''
		},
		{
			'id': 9,
			'name': 'cherry',
			'image': 'data:image/jpeg;base64,',
			'unit': 'KG'
		},
		{
			'id': 10,
			'name': 'book',
			'image': 'data:image/jpeg;base64,',
			'unit': 'Unit'
		},
		{
			'id': 11,
			'name': 'cake',
			'image': 'data:image/jpeg;base64,',
			'unit': ''
		}
	];
    
	public isItemSelected: boolean = false;
	public selectedProduct: Product = null;
	public allChosenProducts: Array<any> = [];
	
    public typing: string = 'quantity';
    public quantity: number;
    public price: number;
	public total: number = 0;
    
	constructor(
		public navCtrl: NavController,
		public navParams: NavParams,
		public voucherProvider: VoucherProvider,
	) {
	}

	goToScanPage() {
		this.voucherProvider.setPrice(this.total);
		this.voucherProvider.setChosenProducts(this.allChosenProducts);
		this.navCtrl.push(ScanPage);
	}

	selectProduct(product) {
		if (product == this.selectedProduct) {
			this.clearSelection();
		} else {
            this.clearSelection();
			this.isItemSelected = true;
			this.selectedProduct = product;
		}
	}

	animateDrawer(drawer) {
		drawer.close();
		setTimeout(() => {
			drawer.open();
		}, 300);
	}
    
	addToBasket() {
        if (this.quantity && this.price) {
            this.allChosenProducts.push({
				product: this.selectedProduct,
				quantity: this.quantity,
				price: this.price,
				subTotal: this.price * this.quantity
			});
    		this.allChosenProducts.forEach(element => {
                this.total += element.subTotal
            });
        }
        this.clearSelection();
	}
    
    clearSelection() {
        this.isItemSelected = false;
        this.selectedProduct = null;
        this.typing = 'quantity';
        this.quantity = null;
        this.price = null;
    }

	clearItemList() {
		this.allChosenProducts = [];
		this.total = 0;
	}
}



